import _ from 'lodash';
import axios from 'axios';
let config = require('../../config');

let backendConfigurer = function () {
  switch (process.env.NODE_ENV) {
    case 'testing':
    case 'development':
      return 'http://' + config.dev.backendHost + ':' + config.dev.backendPort;
    case 'production':
      return 'https://' + config.build.backendHost + ':' + config.build.backendPort;
  }
}

let backendUrl = backendConfigurer();

let AXIOS = axios.create({
  baseURL: backendUrl,
  headers: {'Access-Control-Allow-Origin': frontendUrl}
});

export default {
  name: 'eventregistration',
  data () {
    return {
      persons: [],
      events: [],
      newPerson: '',
      personType: 'Person',
      newEvent: {
        name: '',
        date: '2017-12-08',
        startTime: '09:00',
        endTime: '11:00',
        title: ''
      },
      paypal: {
        email: '',
        amount: ''
      },
      selectedPerson: '',
      selectedPersonToPay: '',
      selectedPerformer: '',
      selectedEvent: '',
      selectedEventToAssign: '',
      selectedEventToPay: '',
      errorPerson: '',
      errorEvent: '',
      errorRegistration: '',
      roles: [
        {
          value: 'Person',
          label: '1'
        },
        {
          value: 'Performer',
          label: '2'
        }
      ],
      response: []
    }
  },
  created: function () {
    // Initializing persons
    AXIOS.get('/persons')
    .then(response => {
      this.persons = response.data;
      this.persons.forEach(person => this.getRegistrations(person.name))
    })
    .catch(e => { this.errorPerson = e });
    AXIOS.get('/performers')
    .then(response => {
      this.persons += response.data;
      this.persons.forEach(person => this.getRegistrations(person.name))
    })
    .catch(e => { this.errorPerson = e });
    AXIOS.get('/events').then(response => { this.events = response.data }).catch(e => { this.errorEvent = e });
    AXIOS.get('/theatres').then(response => { this.events += response.data }).catch(e => { this.errorEvent = e });
  },

  methods: {

    createPerson: function (personType, personName) {
      if (personType === 'Person') {
        AXIOS.post('/persons/'.concat(personName), {}, {})
      .then(response => {
        this.persons.push(response.data);
        this.errorPerson = '';
        this.newPerson = '';
      })
      .catch(e => {
        e = e.response.data.message ? e.response.data.message : e;
        this.errorPerson = e;
        console.log(e);
      });
      } else {
        AXIOS.post('/performer/'.concat(personName), {}, {})
      .then(response => {
        this.persons.push(response.data);
        this.errorPerson = '';
        this.newPerson = '';
      })
      .catch(e => {
        e = e.response.data.message ? e.response.data.message : e;
        this.errorPerson = e;
        console.log(e);
      });
      }
    },

    createEvent: function (newEvent) {
      if (newEvent.title !== '') {
        AXIOS.post('/events/'.concat(newEvent.name), {}, {params: newEvent})
      .then(response => {
        this.events.push(response.data);
        this.errorEvent = '';
        this.newEvent.name = this.newEvent.date = this.newEvent.startTime = this.newEvent.endTime = '';
      })
      .catch(e => {
        e = e.response.data.message ? e.response.data.message : e;
        this.errorEvent = e;
        console.log(e);
      });
      } else {
        AXIOS.post('/theatres/'.concat(newEvent.name), {}, {params: newEvent})
        .then(response => {
          this.events.push(response.data);
          this.errorEvent = '';
          this.newEvent.name = this.newEvent.date = this.newEvent.startTime = this.newEvent.endTime = this.newEvent.title = '';
        })
        .catch(e => {
          e = e.response.data.message ? e.response.data.message : e;
          this.errorEvent = e;
          console.log(e);
        });
      }
    },

    registerEvent: function (personName, eventName) {
      let event = this.events.find(x => x.name === eventName);
      let person = this.persons.find(x => x.name === personName);
      let params = {
        person: person.name,
        event: event.name
      };

      AXIOS.post('/register', {}, {params: params})
      .then(response => {
        person.eventsAttended.push(event)
        this.selectedPerson = '';
        this.selectedEvent = '';
        this.errorRegistration = '';
      })
      .catch(e => {
        e = e.response.data.message ? e.response.data.message : e;
        this.errorRegistration = e;
        console.log(e);
      });
    },

    getRegistrations: function (personName) {
      AXIOS.get('/events/person/'.concat(personName))
      .then(response => {
        if (!response.data || response.data.length <= 0) return;

        let indexPart = this.persons.map(x => x.name).indexOf(personName);
        this.persons[indexPart].eventsAttended = [];
        response.data.forEach(event => {
          this.persons[indexPart].eventsAttended.push(event);
        });
      })
      .catch(e => {
        e = e.response.data.message ? e.response.data.message : e;
        console.log(e);
      });
      AXIOS.get('/events/performer/'.concat(personName))
      .then(response => {
        if (!response.data || response.data.length <= 0) return;

        let indexPart = this.persons.map(x => x.name).indexOf(personName);
        this.persons[indexPart].eventsAttended = [];
        response.data.forEach(event => {
          this.persons[indexPart].eventsAttended.push(event);
        });
      })
      .catch(e => {
        e = e.response.data.message ? e.response.data.message : e;
        console.log(e);
      });
    },

    assignEvent: function (performerName, eventName) {
      let performer = this.persons.find(x => x.name === performerName)
      let event = this.events.find(x => x.name === eventName)
      let params = {
        performer: performer.name,
        event: event.name
      };
      AXIOS.post('/assign', {}, {params: params})
      .then(response => {
        performer.eventsPerformed.push(event)
        this.selectedEventToAssign = '';
        this.selectedPerformer = '';
        this.errorRegistration = '';
      })
      .catch(e => {
        e = e.response.data.message ? e.response.data.message : e;
        this.errorRegistration = e;
        console.log(e);
      });
    },

    createPayment: function (paypalEmail, paypalAmount, eventName, personName) {
      let event = this.events.find(x => x.name === eventName);
      let person = this.persons.find(x => x.name === personName);
      let params =
        {
          paypalEmail: paypalEmail,
          paypalAmount: paypalAmount,
          event: event.name,
          person: person.name

        };
      AXIOS.post('/pay', {}, {params: params})
      .then(response =>{
        this.paypal.email = '';
        this.paypal.amount = '';
        this.selectedPersonToPay = '';
        this.selectedEventToPay = '';
      })
      .catch(e => {
        e = e.response.data.message ? e.response.data.message : e;
        this.errorRegistration = e;
        console.log(e);
      });
    }
  }
}



